<template>
    <div id = "chooseCity">

      <mt-index-list :show-indicator="true">

        <div id="header">

          <div id="header-localPosition">

            <img id="header-localPosition-img" src="../../../assets/icon/location.png"/>
            <div class="header-lp-city">
              <div class="header-localPosition-lable header-localPosition-lable1">{{localCity}}</div>
              <div class="header-localPosition-lable header-localPosition-lable2">当前城市</div>
            </div>

          </div>
          <div id="header-hotCity">

            <div id="header-hotCity-center">
              <div class="header-hotCity-lable1">
                热门城市
              </div>
              <div class="header-hotCity-hotBtn">

              </div>
            </div>

          </div>
        </div>

        <mt-index-section v-bind:index="cityName"  v-for="(cityName,index) in cityNameData" id="section">

          <!--<div id="section-header">{{cityName}}</div>-->

          <div v-for="(city,cityIndex) in cityData[cityName]" class="parentCell">

            <div class="wrapper">
              <div @click="selectedCity(city.name, city.id, false)"
                   v-bind:id="city.name" class="cell">
                  {{city.name}}
              </div>
              <div v-if="(cityIndex < (cityData[cityName].length-1)) || isShowChildCity[city.name]" class="sparator"></div>
            </div>

            <div class="wrapper" v-if="isShowChildCity[city.name]" v-for="(childCity,childCityIndex) in city.childCitys">
              <div @click="selectedCity(childCity.name, childCity.id, true)" class="cell cell-child">
                  {{childCity.name}}
              </div>
              <div v-if="(cityIndex < cityData[cityName].length-1) || childCityIndex < city.childCitys.length-1" class="sparator sparator-child"></div>
            </div>

          </div>
        </mt-index-section>
      </mt-index-list>

    </div>
</template>

<script>
    import mapHelper from '@/js/mapHelper'
    export default {
      name: 'chooseCity',
      data () {
        return {
          cityNameData: [
            'C',
            'S'
          ],
          cityData: {
            'C': [{'name': '长沙', 'id': '123', childCitys: [{'name': '芙蓉区', 'id': '1245'}, {'name': '长沙市', 'id': '1246'}]},
                  {'name': '长沙2', 'id': '124'},
                  {'name': '长沙3', 'id': '125', childCitys: [{'name': '芙蓉区', 'id': '1245'}, {'name': '长沙市', 'id': '1246'}]}],
            'S': [{'name': '深圳', 'id': '1'},
                  {'name': '深圳2', 'id': '2'},
                  {'name': '深圳3', 'id': '3'}]
          },
          isShowChildCity: {
            '长沙': false,
            '长沙3': false
          },
          localCity: '正在定位',
          address: {}
        }
      },
      mounted () {
        console.log('mounted')
        var self = this
        mapHelper.getCurrentLocation(function (address) {
          self.address = address
          self.localCity = address.city
        })
      },
      methods: {
        selectedCity (cityName, cityId, isChildCity) {
          var isShow = this.isShowChildCity[cityName]
          if (isShow !== undefined && !isChildCity) {
            this.isShowChildCity[cityName] = !isShow
          } else {
            this.utils.alert('选择了:' + cityName)
          }
        }
      }
    }
</script>

<style>
  #header{
    height: 4.02rem;
  }


  #header-localPosition {
    height: .83rem;
    /*background-color: black;*/
    border-bottom: solid 1px #d8d8d8;
    position:relative;
  }
  #header-localPosition-img {
    width: .21rem;
    display: inline;
    position: absolute;
    left: 20px;
    top: .225rem;
  }
  .header-lp-city {
    display: inline;
    position: absolute;
    left: 40px;
    top: 0px;
    bottom: 0px;
  }
  .header-localPosition-lable {
    display: inline;
    color: #191919;
    line-height: .83rem;
    vertical-align: middle;
    font-size: .26rem;
  }
  .header-localPosition-lable2 {
    color: #CCCCCC;
    font-size: .23rem;
    line-height: .85rem;
  }


  #header-hotCity {
    height: 3.19rem;
    /*background-color: #26a2ff;*/
    position:relative;
  }
  #header-hotCity-center {
    position:absolute;
    top: .63rem;
    bottom: .63rem;
    left: 0rem;
    right: 0rem;

  }
  .header-hotCity-lable1 {
    position:absolute;
    left: 20px;
    font-size: .23rem;
  }
  .header-hotCity-hotBtn {
    position:absolute;
    left: 0px;
    right: 0px;
    bottom: 0px;
    height: 1.47rem;
    background-color: #26a2ff;
  }

  .cell {
    background-color: white;
    text-align: left;
    height: 32px;
    line-height: 32px;
    /*width: 80%;*/
    vertical-align: middle;
    font-size: .26rem;
    padding-left: 20px;
  }
  .cell-child {
    padding-left: 30px;
  }
  .wrapper {
    height: 33px;
    /*width: 80%;*/
  }
  .sparator {
    /*width: 100%;*/
    height: 1px;
    background-color: #d8d8d8;
    margin-left: 9px;
  }
  .sparator-child {
    margin-left: 28px;
  }
  .mint-indexsection-index{
    height: .49rem;
    background-color: #eeeeee;
    line-height: .15rem;
    text-align: left;
    padding-left: 20px;
    color: #bd8d46;
    font-size: .26rem;
  }
  #section-header {
    height: 25px;
    background-color: #eeeeee;
  }
  .mint-indexlist-nav {
    border-left: solid 0px #ddd;
    /*background-color: #26a2ff;*/
  }


</style>



